.App {
  text-align: center;
  background-color: black;
  height: 100vh;
  width: 100%;
  overflow: hidden;
  font-family: 'Overpass', sans-serif;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media screen and (max-width: 450px) {
  .App{
    min-width: 350px;
    flex-direction: column;
    padding: 1%;
  }
}

